<template>
  <v-container fluid class="fill-height hero">
    <v-row>
      <v-col class="d-flex justify-center">
        <div>
          <v-img
            class="mx-auto my-6"
            max-width="228"
            src="../assets/fitoagro_logo.png"
          ></v-img>
          <v-card
            class="mx-auto pa-12 pb-8"
            elevation="8"
            width="28rem"
            rounded="lg"
          >
            <v-card-text>
              <v-form v-model="formValid">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model.trim="password"
                      :rules="rules.password"
                      :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                      :type="visible ? 'text' : 'password'"
                      label="Nova Senha"
                      placeholder="Insira a nova senha"
                      prepend-inner-icon="mdi-lock-plus"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      @click:append-inner="visible = !visible"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model.trim="confirm_password"
                      :rules="rules.confirm_password"
                      :append-inner-icon="
                        confirmVisible ? 'mdi-eye-off' : 'mdi-eye'
                      "
                      :type="confirmVisible ? 'text' : 'password'"
                      label="Confirmar Senha"
                      placeholder="Confirme a nova senha"
                      prepend-inner-icon="mdi-lock-check"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      @click:append-inner="confirmVisible = !confirmVisible"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-btn
                      block
                      size="large"
                      color="primary"
                      variant="tonal"
                      :loading="isLoading"
                      :disabled="!formValid"
                      @click="resetPassword()"
                      >Alterar senha</v-btn
                    >
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "ResetPasswordView",
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      password: "",
      confirm_password: "",
      token: this.$route.query.token,
      rules: {
        password: [
          (v) => !!v || "A senha é obrigatória",
          (v) => v.length >= 8 || "A senha deve ter no mínimo 8 caracteres",
        ],
        confirm_password: [
          (v) => !!v || "A confirmação de senha é obrigatória",
          (v) => v === this.password || "As senhas não coincidem",
        ],
      },
      visible: false,
      confirmVisible: false,
      formValid: false,
      isLoading: false,
    };
  },
  methods: {
    async resetPassword() {
      this.isLoading = true;
      console.log(this.token);
      await axios
        .post("api/resetPassword", {
          password: this.password,
          token: this.token,
        })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar("Senha alterada com sucesso");
          this.$router.push("/login");
        })
        .catch((error) => {
          this.isLoading = false;
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao alterar a senha");
        });
    },
  },
};
</script>

<style scoped>
.hero {
  background: url("../assets/crops_bg.jpg") no-repeat center center;
  background-size: cover;
}
</style>
